import React, { Component } from "react";
import ReactDOM from 'react-dom'
import {NavLink} from 'react-router-dom';
import ShortDesc from './ShortDesc';
import Title from './Title';
import Number from './Number';
import Links from './Links'
import Details from './Details';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import MainImage from './MainImage';
import {CSSTransition,TransitionGroup,Transition} from 'react-transition-group';
let pathname= '/01';





class First extends Component {
   
  render(){
      const prop=this.props.location;
    return (
        <React.Fragment>
        
        <div className='main-container'>
           <Links />
     <NavLink to={this.props.linkto} as='/details' className='sub-container'>
        
        <ReactCSSTransitionGroup transitionName="num" transitionLeaveTimeout={500} transitionEnterTimeout={500} className='num-container'>
        
        <Number  prop={prop} key={prop} hide={this.props.hide}/>
        
        </ReactCSSTransitionGroup>
        
        <ReactCSSTransitionGroup transitionName="num" transitionLeaveTimeout={500} transitionEnterTimeout={500} className='main-title-container'>
        <Title  prop={prop} key={prop}/>
        </ReactCSSTransitionGroup>
        <div className="sub-content">
        
            <div className="keywords">
                <ShortDesc prop={prop} />
            </div>
        
            <div className="see-more">
                <NavLink to={'/'+prop+'/details'} className="see-more-text">See more</NavLink>
            </div>
        
        </div>      
        
        <ReactCSSTransitionGroup transitionName="main-image" transitionLeaveTimeout={500} transitionEnterTimeout={500} className={this.props.containerprop} >
        
        <MainImage prop={prop} classprop={this.props.classprop} key={prop} />
        
        </ReactCSSTransitionGroup>
        
        <div className={this.props.hidenumber} >
            <NavLink className="number" to="/01">01</NavLink>
            <NavLink className="number" to="/02">02</NavLink>
            <NavLink className="number" to="/03">03</NavLink>
            <NavLink className="number" to="/04">04</NavLink>
        </div>
        
    </NavLink>
      
       </div>
        
        <Details prop={this.props.location} hide={this.props.details}/>
        
        </React.Fragment>
    );
  }
}


export default First;


