
import React, { Component } from "react";
import ReactDOM from 'react-dom';

class MainImage extends Component{
    render(){
        const src=process.env.PUBLIC_URL+'/img/'+this.props.prop+'/main.jpg';
        return <img src={src} className={this.props.classprop} align="left" alt="just some img" data-aos="slide-left"  data-aos-duration="600" data-aos-delay="150"/>
    }
}


export default MainImage;






