
import React, { Component } from "react";
import ReactDOM from 'react-dom';
import {NavLink} from 'react-router-dom';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
const Description=["This website is a blog platform created for topics like Travel, Technology, Design. It was a school project made in Scotland where I had my exchange, where we started using Node and MongoDB for back-end.","Martyr’s bay is a restaurant located on the Isle of Iona, during one of our first school projects we had to redesign the existing website of this restaurant. We also rebranded the company giving it a fresh look mixed with nautical motives. For this website the Material Design was used.","Huset KBH is a non profit organisation in Copenhagen where they are organising several events. We needed to create a platform for easy usage for the vast target audience and for the partners to create the event. Wordpress was used in the making of this website.","This website was an attempt to delve deeper into JavaScript. The website consists of 3 different cipher methods which encode and also decode messages. The ciphers used were from WWI."]
class Details extends Component{
    render(){
        let firstIMG=process.env.PUBLIC_URL+'/img/'+this.props.prop+'/01.jpg';
        let Video= process.env.PUBLIC_URL+'/img/'+this.props.prop+'/'+this.props.prop+'.mp4';
        let secondIMG=process.env.PUBLIC_URL+'/img/'+this.props.prop+'/02.jpg';
        return (
            <React.Fragment>
             
            <div className={this.props.hide}>
            <div className="mouse" data-aos="fade-out" data-aos-easing="ease-in-sine" data-aos-duration="400"><img src={process.env.PUBLIC_URL+'/mouse.svg'}/><p>Scroll Down</p></div>
            <p className="description-long" data-aos="zoom-out-right" data-aos-easing="ease-in-sine" data-aos-duration="300">{Description[Number(this.props.prop)-1]}</p>
           
        <div>
            
        <img src={firstIMG} className="img" data-aos="zoom-out" data-aos-easing="ease-in-sine" data-aos-duration="300" />
        <video className="img" autoplay="autoplay" loop="loop" data-aos="zoom-out" data-aos-easing="ease-in-sine" data-aos-duration="300">
        <source src={Video} type="video/mp4"/>
            </video>
        <img className="img" src={secondIMG} data-aos="zoom-out" data-aos-easing="ease-in-sine" data-aos-duration="300"/>
        </div>
        </div>
        </React.Fragment>
)
    }
}


export default Details;



