import React, { Component } from "react";
import ReactDOM from 'react-dom';
const mainTitles=[
    ["Project at","Napier"],
    ["Martyr's bay","Restaurant"],
["Huset KBH","Redesign"],["Encryption","Page"]
];


class Title extends Component{
    
    render(){
        return <div className='main-title-container' data-aos="zoom-in" data-aos-duration="300"><h1 className='main-title'>{mainTitles[Number(this.props.prop.substr(1))-1][0]}</h1><h1 className='main-title'>{mainTitles[Number(this.props.prop.substr(1))-1][1]}</h1></div>
    }
}

export default Title;


