import React, {Component} from 'react';
import {NavLink} from 'react-router-dom';
class Links extends Component{
    render(){
        return(
            <React.Fragment>
                    <NavLink className="menu projects" to="/01#">projects</NavLink>
                    <NavLink className="menu about" to="/about">about</NavLink>
            </React.Fragment>
        )
    }
}

export default Links;


