import React, { Component } from "react";
import Links from './Links';
import {NavLink} from 'react-router-dom';

 
class About extends Component {
  render() {
    return (
      <div className="main-container">
             <Links />
        <div className="sub-container">
        <h1 className='about-title'>Hi, I'm Tamas</h1>
        <p className="p-text">My name is Tamas Istvan Torok and I am a Multimedia Design and Communication student in Copenhagen, Denmark. Currently second year at KEA, I am passionate about web development. I was studying for half a year in Edinburgh at Napier University, where I learned about back-end development as well. I like to create websites, and explore innovative ways to bridge design and coding.<br></br><br></br>
        I'm currently looking for a 2 month internship between August and October 2018.<br></br><br></br>   <a className="mailto" href="mailto:thomasstorok@gmail.com">thomasstorok@gmail.com</a><br></br><br></br><a className="resume" href={process.env.PUBLIC_URL+'/resume.pdf'}>my resume</a></p>
      <div className="about-loader"><img src={process.env.PUBLIC_URL+'loader.svg'}/></div>
        </div>
      </div>
    );
  }
}


export default About;


