import React, { Component } from "react"
import {Route,Switch,HashRouter as Router
       ,Redirect} from 'react-router-dom';
import Home from './Home';
import About from './About';
import {
        TransitionGroup,
        CSSTransition
} from 'react-transition-group';
import Links from './Links';
import Single from './Single';
import Details from './Details';
require('aos/dist/aos.css');

 
class Main extends Component {
componentDidMount(){
      const ele = document.getElementById('loader-container')
      if(ele){
        ele.classList.add('available')
        setTimeout(() => {
          ele.outerHTML = ''
        }, 2520)
      }
    }

  render() {
       
    return (
    <Router>

      <Route render={ ({location,match})=> (
        <React.Fragment>
      <Redirect exact from="/" to="/01"/>    
      <Route exact path="/about" component={About} />
      
      <Route exact path="/01" render={
        ()=><Home match={match}  location={location.pathname.substr(1) } classprop="main-image" titleprop="main-title" containerprop="main-image-container" hide="num" hidenumber="numbers" details="details-content hide none" linkto={location.pathname.substr(0,3)+'/details'}/> }/>
      <Route exact path="/02" render={
        ()=><Home match={match}  location={location.pathname.substr(1) } classprop="main-image" titleprop="main-title" containerprop="main-image-container" hide="num" hidenumber="numbers" details="details-content hide none" linkto={location.pathname.substr(0,3)+'/details'}/> }/>
      <Route exact path="/03" render={
        ()=><Home match={match}  location={location.pathname.substr(1) } classprop="main-image" titleprop="main-title" containerprop="main-image-container" hide="num" hidenumber="numbers" details="details-content hide none" linkto={location.pathname.substr(0,3)+'/details'}/> }/>
      <Route exact path="/04" render={
        ()=><Home match={match}  location={location.pathname.substr(1) } classprop="main-image" titleprop="main-title" containerprop="main-image-container" hide="num" hidenumber="numbers" details="details-content hide none" linkto={location.pathname.substr(0,3)+'/details'}/> }/>
      
      
        <Route path="/:number/details" render={
        ()=><Home match={match}  location={location.pathname.substr(1,2)} classprop="main-image main-image-details" titleprop="main-title"  containerprop="main-image-container main-image-cont-details" hide="hide num" hidenumber="hide numbers" details="details-content" linkto={location.pathname.substr(0,3)} />} />
      
        
          
         </React.Fragment> )}           />  

    </Router>   

    );
  }
}
 
export default Main;





